.contactInfoContainer {
    flex:0.25;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    background-color: #f7f8f9;
    padding: 1rem;
}

.contactInfo__top{
    flex:0.3;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-top: 1rem;
    h5{
        margin-top: 0.5rem;
    }
    p{
        opacity: 0.6;
        margin-top: 0.3rem;
        font-size: 0.8rem;
    }
    .contactInfo__icons{
        display: flex;
        align-items: center;
        margin-top: 0.5rem;
        .MuiSvgIcon-root{
            opacity: 0.5;
            position: relative;
            cursor: pointer;
            font-size: 1rem;
          }
    }
}

.contactInfo__center{
    flex:0.4;
    display: flex;
    flex-direction: column;
    font-size: 0.8rem;
    .contactInfo__card{
        display: flex;
        flex-direction: column;
        margin-top: 0.2rem;
        padding: 0.5rem;
        box-shadow: 0 25px 50px -12px rgba(0, 0, 0, 0.25);
        p{
            font-size: 0.8rem;
            font-weight: normal;
        }
        .contactInfo__connection{
            display: flex;
            margin-top: 0.5rem;
            width:100%;
            .MuiSvgIcon-root{
                opacity: 0.5;
                position: relative;
                cursor: pointer;
                margin-right: 1rem;
              }
        }
    }
}

.contactInfo__title{
    font-size: 0.8rem;
    font-weight: 500;
}

.contactInfo__bottom{
    flex: 0.3;
    display: flex;
    flex-direction: column;
    .media__card{
        display: flex;
        align-items: center;
        background: white;
        margin-top: 0.5rem;
        padding: 0.5rem;
        box-shadow: 0 25px 50px -12px rgba(0, 0, 0, 0.25);
    }
}

@mixin SquareIcon($color){
    width: 2rem;
    height: 2rem;
    background: $color;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    z-index: 0;
    margin: 0 0.5rem;
    border-radius: 0.25rem;
}

.OrangeSquare{
    @include SquareIcon(#ff918c)
}

.GreenSquare{
    @include SquareIcon(#5cc6ca)
}

.BlueSquare{
    @include SquareIcon(#77aaeb)
}
.format{
    position: absolute;
    z-index: 1;
    color: white; 
    font-size: 0.6rem;
    font-weight: 400; 

}
